
#homepage-vue-ap {
}

.home-grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
@media (min-width: 960px){
.home-grid {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
}

@font-face {
  font-family: system;
  font-style: normal;
  font-weight: 300;
  src: local(".SFNSText-Light"), local(".HelveticaNeueDeskInterface-Light"), local(".LucidaGrandeUI"), local("Ubuntu Light"), local("Segoe UI Light"), local("Roboto-Light"), local("DroidSans"), local("Tahoma");
}
body {
  font-family: "system";
  line-height: 1.6rem;
}
.category-header {
  background: black;
  color: white;
  font-size: 1.2rem;
  padding: 1rem;
  font-weight: bold;
  text-transform: capitalize;
}
.action-button {
  background: none;
  border: 0;
  cursor: pointer;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-user-select: none; /* for button */
     -moz-user-select: none;
      -ms-user-select: none;
  display: block;
  width: 100%;
  height: 3rem;
  font-size: 1rem;
  background: #DDD;
  font-weight: bold;
}
.action-button:hover {
  background: #EFEFEF;
}
.action-button:focus {
  outline: none;
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
.post {
  border-bottom: 1px solid #CCC;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
}
.post:after {
  content: "";
  display: table;
  clear: both;
}
.meta  {
  font-size: 0.8rem;
  line-height: 1rem;
}
.small-thumbnail {
  float: left;
  width: 25%;
  padding-right: 1rem;
}
.small-content {
  float: left;
  width: calc(75% - 1rem);
}
.small-content h2 {
  font-size: 1.2rem;
  margin-top: 0;
}
.post img {
  width: 100%;
  max-width: 100%;
}

















